.sidebar {
  background-color: var(--slack-color);
  border-top: 1px solid #49274b;
  max-width: 260px;
  color: #fff;
  flex: 0.3;
}

.sidebar__header {
  display: flex;
  border-bottom: 1px solid#49274b;
  padding: 13px;
}

.sidebar__header > .MuiSvgIcon-root {
  padding: 8px;
  font-size: 18px;
  color: #49274b;
  background-color: #fff;
  border-radius: 999px;
}

.sidebar__info {
  flex: 1;
}

.sidebar__info > h2 {
  font-size: 15px;
  font-weight: 900;
  margin-bottom: 10px;
}

.sidebar__info > h3 {
  display: flex;
  align-items: center;
  font-weight: 100;
  font-size: 12px;
}

.sidebar__info > h3 > .MuiSvgIcon-root {
  font-size: 15px !important;
  margin-top: 1px;
  margin-right: 2px;
  color: rgb(5, 223, 5);
}
